.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: #8a2b06;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}

.header ul {
  display: flex;
}

.header li {
  text-decoration: none;
  list-style: none;
  padding: 15px;
}

.header a {
  text-decoration: none;
  color: white;
}


